import { ReactElement, useState } from 'react';
import { Book, Config, createApplication, File, getPayload, request, Summary, useAsyncEffect } from '@topwrite/common';
import { createHistory } from '@/lib/history';
import * as models from '@/models';
import Main from '@/components/main';

async function loadLocaleData(locale: string) {
    switch (locale) {
        case 'en':
            return import('../lang/en.json');
        default:
            return import('../lang/zh-CN.json');
    }
}

export default function App() {
    const [app, setApp] = useState<ReactElement>();

    useAsyncEffect(async () => {

        try {
            const history = createHistory();

            let payload = getPayload();

            if (!payload) {
                const response = await request.get(history.location.pathname);
                payload = response.data;
            }

            let { id, summary, file, config = {}, options = {} } = payload;

            config = Config.createFromObject(config);

            summary = typeof summary === 'string' ? Summary.createFromText(summary) : Summary.createFromObject(summary);

            const initialState = {
                book: new Book(id, config, summary),
                file: new File(file),
                options
            };

            const Application = await createApplication('reader', models, initialState, loadLocaleData);

            setApp(<Application><Main /></Application>);
        } catch (e: any) {
            console.error(e);
            if (request.isAxiosError(e)) {
                setApp(e.response?.data || e.message);
            } else {
                setApp(e.message);
            }
        }
    }, []);

    if (app) {
        return app;
    }

    return null;
}
