import { ImgHTMLAttributes } from 'react';
import { isUrl, useOptions } from '@topwrite/common';
import useFile from '@/lib/use-file';

const Image = ({ src, ...props }: ImgHTMLAttributes<HTMLImageElement>) => {

    const { lfs } = useOptions();
    const file = useFile();

    if (lfs && src && !isUrl(src)) {
        const { url, files } = lfs;
        const filename = file.relative(decodeURIComponent(src));
        const oid = files[filename];
        if (oid) {
            src = url.replace('{oid}', oid);
        }
    }

    return <img src={src} {...props} />;
};

export default Image;
